.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


@keyframes identifier  {
  0%   {background-position: 0 0;}
  25% {background-position: 50% 0;}
  50% {background-position: 90% 0;}
  60% {background-position: 60%;}
  75% {background-position: 40%;}
  100%  {background-position: 0 0;}
}


.tileimg {
  display: block;
  width: 100%;
  height: auto;
  opacity: 1;
  -webkit-transition-property: opacity;
  transition-property: opacity;
  -webkit-transition-duration: 0.125s;
  transition-duration: 0.125s;
  -webkit-transition-timing-function: ease-in;
  transition-timing-function: ease-in;
  box-shadow: rgb(0 0 0 / 20%) 0px 54px 55px, rgb(0 0 0 / 9%) 0px -12px 30px, rgb(0 0 0 / 9%) 0px 4px 6px, rgb(0 0 0 / 14%) 0px 12px 13px, rgb(0 0 0 / 6%) 0px -3px 5px; 
}


.tileimg:hover{
  top: -1rem;
  left: -1rem;
  box-shadow: 5px 5px rgb(244 170 200 / 40%), 10px 10px rgb(244 170 200 / 30%), 15px 15px rgb(244 170 200 / 20%), 20px 20px rgb(244 170 200 / 10%), 25px 25px rgb(244 170 200 / 5%);

}


body{    overflow-x: hidden ;}






