
.container{
  position: relative;
  background: rgba(255, 255, 255, 0.05);
  box-shadow:0 5px 15px rgba(0, 0, 0, 1)  ;
}
/*form light gray 1/2 background*/
.container:before{
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
 background: rgba(255, 255, 255, 0.1); 
 pointer-events: none;
}
/* form content  and styling*/
.form  {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 40px;
  box-sizing: border-box;
  z-index: 1;
}

.form h2{
  text-align: center;
  margin: 0;
  padding: 0;
  color: #fff;
  font-size: 24px;
}

.form .inputBox input{
width: 100%;
background: transparent;
border: none;
border-bottom: 2px solid #ffffff;
outline: none;
color: #fff;
font-size: 18px;
padding: 5px 0;
margin-top: 30px;
}





  

::placeholder{
  color: #eee;
}

.form .inputBox input[type="submit"]{
 margin-left:auto ;
  background: #fff;
  color: #000000;
  border: none;
  border-radius: 5px;
  font-weight: 900;
 max-width: 100px;
 cursor: pointer;
}



.form p {
 color: #eee;
 text-align: center;
}

.form p a{
  text-align: center;
  text-decoration: none;
  color: #fff;
}
/* form glowing background*/
.container::after{
  content: '';
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  background: linear-gradient(45deg,#ff0047,#6eff00);
  pointer-events: none;
  animation: animate 10s linear infinite;
}
@keyframes animate {
  0%{
      filter: blur(50px) hue-rotate(0deg);
  }
  100%{
     filter: blur(50px) hue-rotate(360deg);
 }
}