

.button {
  cursor: pointer;
  border: none;
  border-radius: 20px;
  position: relative;
  overflow: hidden;
  padding: 0;
  letter-spacing: 0.5px;
  outline: none;
  transition: 0.3s;


}
.button:before, .button:after {
  content: '';
  position: absolute;
  width: 20%;
  height: 15px;
  transition: 0.3s ease-out;
  opacity: 0.72;
}
.button:before {
  animation: 6s blob1 infinite linear;
}
.button:after {
  animation: 6s blob2 infinite linear;
}
.button:hover:before, .button:focus:before, .button:hover:after, .button:focus:after {
  opacity: 1;
  width: 50%;
  height: 40px;
}
.button span {
  position: relative;
  display: block;
  padding: 12px 28px;
  backdrop-filter: blur(7px);
  z-index: 1;
}









@keyframes blob1 {
  0%, 100% {
    top: 8px;
    left: 10px;
 }
  25% {
    top: 16px;
    left: 16px;
 }
  55% {
    top: 12px;
    left: 24px;
 }
}
@keyframes blob2 {
  0%, 100% {
    top: 15px;
    right: 15px;
 }
  33% {
    top: 8px;
    right: 6px;
 }
  66% {
    top: 12px;
    right: 24px;
 }
}
